import React from "react";
import "./home.css";
// import hero from "../../images/heroimg.png";
import cv from "../../images/Dipak Pawar.pdf"
// import video from "../../images/Videos/black.mov"

import Hero from "../../images/sitting.svg"
import wave from "../../images/hii png.gif"
import Typing from "./Typing";
const Home = () => {
  return (
    <>
  
    <div className="home-container" id="home">
      
      <div className="h-leftdiv">
        <div className="h-left-name">
          <img  className="l-title" src={wave} alt="" />
          <h1>
            I'm <span className="my-name"> Dipak Pawar</span>
          </h1>
          <h4 className="web-developer"><Typing/></h4>
        </div>
        <a href={cv} download>
          <button className="cv-button">Download CV</button>
          </a>
 
      </div>
      <div className="h-rightdiv">
        <img className="hero-img" src={Hero} alt="" />
      </div>
    </div>
    </>
  );
};

export default Home;
