import "./about.css";
import React from "react";
import aboutpic from "../../images/About-Avatar.png";
import github from "../../images/Skills/github.png"
import linkdin from "../../images/linkedin.png"

const About = () => {
  return (

    <div id="About">
          <div>
        <img className="about-pic" src={aboutpic} alt="" />
      </div>


      <div >
        <h1 className="about-title">About Me,</h1>
        <div className="about-des">
          Aspiring Full Stack Developer. Capable of writing production-ready
          code using Reactjs ,Redux, CSS on the frontend and NodeJs ,Express at
          the backend to build single page application.
        </div>
        <div className="social-icons">
          <a href="https://github.com/itsDipaks">
          <img src={github} alt="" />
          </a>
   <a href="https://www.linkedin.com/in/dipak-pawar-550776221/">
   <img src={linkdin} alt="" />
   </a>
        </div>
      </div>

  
    </div>

  );
};

export default About;
