import React from "react";
import css from "../../images/Skills/icons8-css3-480.png";
import javascrift from "../../images/Skills/javascript.png";
import mongodb from "../../images/Skills/icons8-mongodb.png";
import html from "../../images/Skills/icons8-html-5-480.png";
import nodejs from "../../images/Skills/nodejs-.png";
import redux from "../../images/Skills/icons8-redux-480.png";
import github from "../../images/Skills/icons8-github-480.png";
import chakraui from "../../images/Skills/icons8-chakra-ui.png";
import bootstrap from "../../images/Skills/icons8-bootstrap-480.png";
import exress from "../../images/Skills/express.png";
import reactpng from "../../images/Skills/react.png";
import "./skills.css";
import Softskills from "./Softskills";
const Skills = () => {
  return (
    <div id="skills">
      <h1 className="skill-title">Technical Skills</h1>


      <div className="skill-div">
        
        <div>
          <img src={javascrift} alt="" />
          <h3>Javascript</h3>
        </div>

        <div>
          <img src={css} alt="" />
          <h3>CSS</h3>
        </div>
        <div>
          <img src={reactpng} alt="" />
          <h3>React</h3>
        </div>
        <div>
          <img src={redux} alt="" />
          <h3>Redux</h3>
        </div>
        <div>
          <img src={github} alt="" />
          <h3>Github</h3>
        </div>
        <div>
          <img src={html} alt="" />
          <h3>HTML</h3>
        </div>
        <div>
          <img src={nodejs} alt="" />
          <h3>NodeJs</h3>
        </div>
        <div>
          <img src={exress} alt="" />
          <h3>Express js</h3>
        </div>
        <div>
          <img src={mongodb} alt="" />
          <h3>Mongodb</h3>
        </div>
        <div>
          <img src={chakraui} alt="" />
          <h3>Chakra Ui</h3>
        </div>
        <div>
          <img src={bootstrap} alt="" />
          <h3>Bootstrap</h3>
        </div>
      </div>

      <Softskills/>
    </div>
  );
};

export default Skills;
