import React from 'react'
import Typewriter from 'typewriter-effect';
import "./type.css"
const Typing = () => {
  return (
    <div className='type-div'>
        
        <Typewriter className='type-div'
  options={{
    strings: ['Developer', 'Full Stack Web-Developer','MERN Stack Developer'],
    autoStart: true,
    loop: true,
  }}
/>
    </div>
  )
}

export default Typing