import React from "react";
// import logo from "../../images/logo.png"
import "./navbar.css";
import Moon from "../../images/moon2.png";
import Sun from "../../images/Sun.png";
import {Link} from "react-scroll";
import {useState} from "react";
import {useEffect} from "react";
import d from "../../images/d.png";
// import  NavLink  from 'react-router-dom'
const Navbar = () => {
  const [theam, setTheam] = useState("darktheam");
  const toogletheam = () => {
    theam === "lightheam" ? setTheam("darktheam") : setTheam("lightheam");
  };

  // console.log(theam)

  useEffect(() => {
    document.body.className = theam;
  }, [theam]);
  return (
    <>
      <div className="n-bar">
        <div className="theam-control" onClick={toogletheam}>
          {theam === "darktheam" ? (
            <img src={Sun} alt="" />
          ) : (
            <img src={Moon} alt="" />
          )}
        </div>
        <div className="n-left">
          <img className="logo" src={d} alt="" />
        </div>

        <div className="n-right">
          <ul className="n-ul-list">
            <li>
              <Link
                activeClass="active"
                to="home"
                offset={-100}
                spy={true}
                smooth={true}
              >
                Home
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="About"
                offset={-100}
                spy={true}
                smooth={true}
              >
                About
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="education"
                offset={-100}
                spy={true}
                smooth={true}
              >
                Education
              </Link>
            </li>

            <li>
              <Link
                activeClass="active"
                to="projects"
                offset={-60}
                spy={true}
                smooth={true}
              >
                Projects
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="skills"
                offset={-100}
                spy={true}
                smooth={true}
              >
                Teck stack
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="contact"
                offset={-100}
                spy={true}
                smooth={true}
              >
                {" "}
                <button className="contact-button">Contect Me</button>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default Navbar;
