import Aos from "aos";
import React, {useEffect} from "react";
import "./education.css";
const Education = () => {
  useEffect(() => {
    Aos.init({
      duration: 900,
      delay: 0,
    });
  }, []);

  return (
    <div id="education">
      <h1 className="e-title">Educational Qualification</h1>
      <hr className="hr" />
      <div data-aos="fade-right" className="e-first-div">
        <h1>Full Stack Web-Developer</h1>
        <h2>Masai School</h2>
        <h5>Jan/2022 - Nov/2022</h5>
      </div>
      <div data-aos="fade-left" className="e-first-div" id="second">
        <h1>BE Mechanical Engginear</h1>
        <h2>GGSF College Of Enginearing Nashik</h2>
        <h5>Aug/2018 - Nov/2021</h5>
      </div>
      <div data-aos="fade-right" className="e-first-div">
        <h1>Diploma In Mechanical Engg.</h1>
        <h2>Amrutvahini Polytechnic Sangamner</h2>
        <h5>Aug/2014 - Nov/2017</h5>
      </div>
    </div>
  );
};

export default Education;
